.container {
  border: 1px solid #d3d9de;
}

.ui {
  width: 100%;
  max-width: 1250px;
  margin: 0 auto;
  padding: 20px 16px;
}
@media (max-width: 767px) {
  .ui {
    padding: 16px 8px;
  }
}

.ui-second-title {
  margin-top: 40px;
}

.ui-btn-list {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}
.ui-btn-list > * {
  flex-shrink: 0;
}

.ui-filter-list {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin-bottom: 20px;
}

.ui-table {
  scrollbar-width: none;
  overflow-x: scroll;
}
.ui-table::-webkit-scrollbar {
  display: none;
}

.ui-label-city {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}
.ui-label-city > * {
  width: 100%;
  overflow: hidden;
}
.ui-label-city div > *:not(:last-child) {
  margin-bottom: 8px;
}

.ui-header {
  border: 1px solid #d3d9de;
}

.ui-profile-card-list {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  gap: 10px;
}

.ui-modal {
  overflow: hidden;
  border-radius: 12px;
}
.ui-modal .overlay {
  position: static;
}